  --main-font-family: Noto Serif, serif;
}

.visually_hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 1;
  visibility: visible;
  background: rgba(0, 0, 0, .2);
  transition: opacity .5s, visibility .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.header {
  width: 1280px;
