}

.contacts {
  background-image: linear-gradient(rgba(0, 0, 0, 0) 20%, #739a77 17%);
}

.contact-title {
  color: #007581;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.contact-title:before, .contact-title:after {
  content: "";
  height: 2px;
  vertical-align: middle;
  width: 20%;
  display: inline-block;
  position: relative;
}

.contact-title:before {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(to right, #00ff72, #2f4858);
  margin-left: -50%;
  right: .5em;
}

.contact-title:after {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(to right, #2f4858, #00ff72);
  margin-right: -50%;
  left: .5em;
}

.contact-map {
  width: 100%;
  height: 150px;
}

