.btn {
  color: #fff;
  cursor: pointer;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  border: 3px solid #ffe081;
  border-radius: 20px;
  padding: .9rem 1.8rem;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  text-decoration: none;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.btn:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #ffe081;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.btn:hover {
  color: #739a77;
  transform: translateX(0);
}

.btn:hover:before {
  transform: translateX(0);
}

.button__label {
